 /* eslint-disable */
import Vue from 'vue'
export default new Vue({
    data: {
        wxInfo: {}
    },
    methods: {
        config (data) {
            this.wxInfo = data
            wx.error((res) => {
                WeixinJSBridge.log(res) // configerrorconfigdebugresSPA
            })
            wx.config({
                debug: false, // ,apialertpclogpc
                appId: data.appId, // 
                timestamp: data.timeStamp, // 
                nonceStr: data.nonceStr, // 
                signature: data.signature, // 1
                jsApiList: ['checkJsApi', 'chooseWXPay'] // JSJS2
            })
        },
        ready () {
            return new Promise((resolve, reject) => {
                wx.ready(() => {
                    resolve(true)
                })
            })
        },
        chooseWXPay () {
            return new Promise((resolve, reject) => {
                wx.chooseWXPay({
                    timestamp: this.wxInfo.timeStamp, // jssdktimestamptimeStampS
                    nonceStr: this.wxInfo.nonceStr, //  32 
                    package: this.wxInfo.package, // prepay_idprepay_id=***
                    signType: this.wxInfo.signType, // 'SHA1''MD5'
                    paySign: this.wxInfo.paySign, // 
                    success: (res) => {
                        WeixinJSBridge.log(res);
                        resolve(res)
                    },
                    cancel (msg) {
                        WeixinJSBridge.log(msg);
                        reject({
                            reason: 'cancel',
                            msg: msg
                        })
                    },
                    fail (err) {
                        WeixinJSBridge.log(err);
                        reject({
                            reason: 'error',
                            msg: err
                        })
                    }
                })
            })
        },
        checkJsApi () {
            return new Promise((resolve, reject) => {
                wx.checkJsApi({
                    jsApiList: ['chooseWXPay'], // JSJS2,
                    success (res) {
                        resolve(res)
                    }
                })
            })
        }
    }
})



